.list {
  list-style: none;
  padding-left: 0;
  margin: 0;
}

.loader {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-sizing: border-box;
  display: inline-block;
  width: 50px;
  height: 80px;
  border-top: 5px solid #fff;
  border-bottom: 5px solid #fff;
  position: relative;
  background: linear-gradient(#FF3D00 30px, transparent 0) no-repeat;
  background-size: 2px 40px;
  background-position: 50% 0px;
  animation: spinx 5s linear infinite;
}

.loader:before,
.loader:after {
  content: "";
  width: 40px;
  left: 50%;
  height: 35px;
  position: absolute;
  top: 0;
  transform: translatex(-50%);
  background: rgba(255, 255, 255, 0.4);
  border-radius: 0 0 20px 20px;
  background-size: 100% auto;
  background-repeat: no-repeat;
  background-position: 0 0px;
  animation: lqt 5s linear infinite;
}

.loader:after {
  top: auto;
  bottom: 0;
  border-radius: 20px 20px 0 0;
  animation: lqb 5s linear infinite;
}

@keyframes lqt {

  0%,
  100% {
    background-image: linear-gradient(#FF3D00 40px, transparent 0);
    background-position: 0% 0px;
  }

  50% {
    background-image: linear-gradient(#FF3D00 40px, transparent 0);
    background-position: 0% 40px;
  }

  50.1% {
    background-image: linear-gradient(#FF3D00 40px, transparent 0);
    background-position: 0% -40px;
  }
}

@keyframes lqb {
  0% {
    background-image: linear-gradient(#FF3D00 40px, transparent 0);
    background-position: 0 40px;
  }

  100% {
    background-image: linear-gradient(#FF3D00 40px, transparent 0);
    background-position: 0 -40px;
  }
}

@keyframes spinx {

  0%,
  49% {
    transform: rotate(0deg);
    background-position: 50% 36px;
  }

  51%,
  98% {
    transform: rotate(180deg);
    background-position: 50% 4px;
  }

  100% {
    transform: rotate(360deg);
    background-position: 50% 36px;
  }
}

/* Styles.css */
.load-More {
  color: #ffffff;
  display: none;
  font-size: 18px;
  background-color: #118bee;
  transition: filter 0.3s ease; 
  cursor: pointer;
  border-radius: 20px;
}

.load-More:hover {
  filter: brightness(1.2); 
}

.container {
  height: 25vh; 
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Contenedor para centrar el formulario en la parte superior */
.form-container {
    width: 100%; 
    display: flex;
    justify-content: center; 
    align-items: flex-start; 
    background-color: #000000;
    padding-top: 20px; 
  }

  :root {
    --timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    --animation-duration: 250ms;
  }
  
  .gallery {
    display: flex;
    flex-wrap: wrap;
    margin-top: 25px;
    gap: 25px;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    list-style-type: none;
    padding-left: 70px;
    padding-right: 70px;
  }
  
  .gallery__item {
    position: relative;
    box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
      0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
    border-radius: 5px;
    overflow: hidden;

      &:hover {
        cursor: pointer;
        box-shadow: 1px 4px 6px 0px rgba(0, 0, 0, 0.16),
              0px 4px 4px 0px rgba(0, 0, 0, 0.06), 0px 1px 1px 0px rgba(0, 0, 0, 0.12);
      }
  }
  .movie-tittle {
    text-transform: uppercase;
    font-size: 12px;
    line-height: calc(16/12);
  }
  .movie-gender{
    font-size: 12px;
    font-weight: 500;
    line-height: calc(14.06/12);
    color: rgba(255, 107, 8, 1);
  }
  .gallery__image:hover {
    transform: scale(1.03);
  }
  
  .gallery__image {
    height: 100%;
    width: 100%;
    object-fit: cover;
    transition: transform var(--animation-duration) var(--timing-function);
    cursor: zoom-in;
  }
  .image-row {
    display: block;
    text-decoration: none;
      height: 100%;
      width: auto;
      object-fit: cover; 
    
  }

  .image-container {
    display: block;
    text-decoration: none;
      height: 100%;
      width: auto;
      object-fit: cover; 
    
  }
  .image-info {
    font-size: small;
    display: flex;
    gap: 10px;
    cursor: zoom-in;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); 
  }
  